import React from "react";
import TechStackList from './TechStackList';


const About = () => {

    const frontEndTechs = [
        {
            name: 'HTML',
            description: '',
        },
        {
            name: 'CSS',
            description: '',
        },
        {
            name: 'JavaScript',
            description: '',
        },
    ];

    const programingTechs = [
        {
            name: 'VB.net',
            description: '',
        },
        {
            name: 'Python',
            description: '',
        },
        {
            name: 'JavaScript',
            description: '',
        },
    ];

    const frameworkTechs = [
        {
            name: 'React',
            description: '',
        },
        {
            name: 'flet',
            description: '',
        },
        {
            name: 'Selenium',
            description: '',
        },
        {
            name: 'Three.js',
            description: '',
        },
    ];

    return (

        <div>
            <h1></h1>
            <h2></h2>

            <TechStackList
                title=""
                techs={frontEndTechs}
                className="feTech-list"
            />

            <TechStackList
                title=""
                techs={programingTechs}
                className="feTech-list"
            />

            <TechStackList
                title=""
                techs={frameworkTechs}
                className="fwTech-list"
            />
        </div>
    );
}

export default About;